import React, { useState, useEffect } from 'react';
import { MapContainer, TileLayer, Marker, Popup, GeoJSON } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet-routing-machine/dist/leaflet-routing-machine.css';
import L from 'leaflet';
import 'leaflet-routing-machine';
import axios from 'axios';

// Fix for default marker icon
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

function App() {
  const [buildings, setBuildings] = useState([]);
  const [rooms, setRooms] = useState([]);
  const [start, setStart] = useState(null);
  const [end, setEnd] = useState(null);
  const [map, setMap] = useState(null);

  useEffect(() => {
    axios.get('/api/buildings/').then(response => setBuildings(response.data.features));
    axios.get('/api/rooms/').then(response => setRooms(response.data.features));
  }, []);

  useEffect(() => {
    if (!map || !start || !end) return;

    const routingControl = L.Routing.control({
      waypoints: [L.latLng(start), L.latLng(end)],
      routeWhileDragging: true,
    }).addTo(map);

    return () => map.removeControl(routingControl);
  }, [map, start, end]);

  const handleMapClick = (e) => {
    if (!start) {
      setStart(e.latlng);
    } else if (!end) {
      setEnd(e.latlng);
    }
  };

  return (
    <>
      <div style={{ position: 'absolute', top: 10, right: 10, zIndex: 1000, background: 'white', padding: 10 }}>
        <input type="text" placeholder="Start" onClick={() => setStart(null)} />
        <input type="text" placeholder="End" onClick={() => setEnd(null)} />
        <button onClick={() => { setStart(null); setEnd(null); }}>Clear</button>
      </div>
      <MapContainer center={[-1.219750072616673, 36.87935953929794]} zoom={18} style={{ height: '100vh', width: '100%' }} whenCreated={setMap} onClick={handleMapClick}>
        <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors' />
        {buildings.map(building => (
          <GeoJSON key={building.id} data={building.geometry} />
        ))}
        {rooms.map(room => (
          <Marker key={room.id} position={[room.geometry.coordinates[1], room.geometry.coordinates[0]]}>
            <Popup>{room.properties.name}</Popup>
          </Marker>
        ))}
      </MapContainer>
    </>
  );
}

export default App;